export const commonMixin = {
    data() {
        return {
            commonShit: {
                // pageMainApi: 'customer',
                editMode: false,
                delConfirmShow: false,
                modalMessage: '',
                dropdownEditShow: false,
                dropdownToEdit: '',
                formClone: '',
                formChangesString: "",
                formCloneDelay: 50,
                activeRow: '',
                activeRowIndex: null,
                activeRowId: null,
                activeRowMessage: '',
                addAvailable: true,
                saveAvailable: true,
                validationOn: true,
            },
            activeTab: 0,
            mainList: [],
            filteredList: [],
            exportedList: [],
            options: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                columnsDropdown: true,
                texts: {
                    count: this.$t("tableTexts.count"),
                    first: this.$t("tableTexts.first"),
                    last: this.$t("tableTexts.last"),
                    filter: this.$t("tableTexts.filter"),
                    filterPlaceholder: this.$t("tableTexts.filterPlaceholder"),
                    limit: this.$t("tableTexts.limit"),
                    page: this.$t("tableTexts.page"),
                    noResults: this.$t("tableTexts.noResults"),
                    filterBy: this.$t("tableTexts.filterBy"),
                    loading: this.$t("tableTexts.loading"),
                    defaultOption: this.$t("tableTexts.defaultOption"),
                    columns: this.$t("tableTexts.columns"),
                  },
            },
        }
        
    },
    methods: {
        activateRow(row) {
            row.rowActive = true;
           this.refreshTablePage();
         },
         deactivateAllRows(table, reference = null) {
           for (var i = 0; i < table.length; i++) {
             table[i].rowActive = false;
           }
           if (reference === null) {
                this.refreshTablePage();
           }
           else {
            this.refreshTablePage(reference);
           }
           
         },
         refreshTablePage(name = "main table") {
            var theTable;
            if (name == "main table")
                theTable = this.$refs.mainTable;
            else 
                theTable = this.$refs[name];
            var currentPage = theTable.Page;
            theTable.setPage(currentPage + 1);
            theTable.setPage(currentPage);
         },
        saveData() {
            if (this.commonShit.editMode == false)
                this.onSubmit();
            else
                this.onUpdateSubmit();
        },
        setTableTexts() {
            this.options.texts.count = this.$t('tableTexts.count');
            this.options.texts.first = this.$t("tableTexts.first");
            this.options.texts.last = this.$t("tableTexts.last");
            this.options.texts.filter = this.$t("tableTexts.filter");
            this.options.texts.filterPlaceholder = this.$t("tableTexts.filterPlaceholder");
            this.options.texts.limit = this.$t("tableTexts.limit");
            this.options.texts.page = this.$t("tableTexts.page");
            this.options.texts.noResults = this.$t("tableTexts.noResults");
            this.options.texts.filterBy = this.$t("tableTexts.filterBy");
            this.options.texts.loading = this.$t("tableTexts.loading");
            this.options.texts.defaultOption = this.$t("tableTexts.defaultOption");
            //this.options.texts.columns = this.$t("tableTexts.columns");
            this.options.texts.columns = '';
          },
        clearActiveMessage() {
            this.commonShit.activeRowMessage = '';
        },
        exportFilteredList() {
            this.exportedList = this.$refs.mainTable.allFilteredData;
            
        }
    },
    created() {
        console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        this.setTableTexts();
    },
    updated() {
        this.setTableTexts();
    }
}